import React from 'react';
import './App.css';
import './signature-pad.css';
import './ie9.css';
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import {Button} from 'primereact/button';
import {Sidebar} from 'primereact/sidebar';
import SignaturePad from 'signature_pad';
import {FileUpload} from 'primereact/fileupload';


class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      visibleFullScreen1:false,
      visibleFullScreen2:false
    }
    this.myUploader = this.myUploader.bind(this);

  }
  savePNG1=()=>{
    if (this.signaturePad1.isEmpty()) {
      alert("Please provide a signature first.");
    } else {
      var dataURL = this.signaturePad1.toDataURL();
      console.log(dataURL);
      console.log(String(dataURL).length);
      this.download(dataURL, "signature.png");
    }
  }
  resetSign1=()=>{
    this.signaturePad1.clear();
  }
  savePNG2=()=>{
    if (this.signaturePad2.isEmpty()) {
      alert("Please provide a signature first.");
    } else {
      var dataURL = this.signaturePad2.toDataURL();
      console.log(dataURL);
      console.log(String(dataURL).length);
      this.download(dataURL, "signature.png");
    }
  }
  resetSign2=()=>{
    this.signaturePad2.clear();
  }
  download=(dataURL, filename)=> {
      var blob = this.dataURLToBlob(dataURL);
      var url = window.URL.createObjectURL(blob);
  
      var a = document.createElement("a");
      a.style = "display: none";
      a.href = url;
      a.download = filename;
  
      document.body.appendChild(a);
      a.click();
  
      window.URL.revokeObjectURL(url);
  }
  dataURLToBlob=(dataURL)=> {
    // Code taken from https://github.com/ebidel/filer.js
    var parts = dataURL.split(';base64,');
    var contentType = parts[0].split(":")[1];
    var raw = window.atob(parts[1]);
    var rawLength = raw.length;
    var uInt8Array = new Uint8Array(rawLength);
  
    for (var i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }
  
    return new Blob([uInt8Array], { type: contentType });
  }
  componentDidUpdate(){
    var ratio =  Math.max(window.devicePixelRatio || 1, 1);
    var canvas1 = document.getElementById("canvas1");
    canvas1.width = canvas1.offsetWidth * ratio;
    canvas1.height = canvas1.offsetHeight * ratio;
    canvas1.getContext("2d").scale(ratio, ratio);
    this.signaturePad1 = new SignaturePad(canvas1);
    // var canvas2 = document.getElementById("canvas2");
    // canvas2.width = canvas2.offsetWidth * ratio;
    // canvas2.height = canvas2.offsetHeight * ratio;
    // canvas2.getContext("2d").scale(ratio, ratio);
    // this.signaturePad2 = new SignaturePad(canvas2);
    window.addEventListener("orientationchange", ()=>this.setState({visibleFullScreen1:false}), false);
  }
  async myUploader(event) {
    //console.log(event.files[0].objectURL);
    let blob= await fetch(event.files[0].objectURL).then(r => r.blob());
    var a = new FileReader();
    a.onload = (e) =>{
      let dataurl=e.target.result;
      //console.log(dataurl);
      this.signaturePad1.clear();
      this.signaturePad1.fromDataURL(dataurl);
      //FileUpload.clear();
      return;
    }
    a.readAsDataURL(blob);
    return;
  }
  render(){
    return (
      <div className="App">
        <header className="App-header">
        <p>Sign PopUp v1.0</p>
        <h3>Please input the following information and sign your name below: </h3>
        <p>First Name: <input type="text"></input>
        <br/>Last Name: <input type="text"></input>
        <br/>Phone No: <input type="text"></input></p>        
        <div>Sign:<br/>
        <Sidebar visible={this.state.visibleFullScreen1} fullScreen={true} baseZIndex={1000000} onHide={()=>this.setState({visibleFullScreen1:false})} showCloseIcon={false}>
        <div className="outer">
        <div className="middle">
          <div id="signature-pad"  className="signature-pad">
          <div className="signature-pad--actions">
                <div>
                    <FileUpload mode='basic' className="upload" maxFileSize={1000000} onSelect={this.myUploader} accept="image/*"  auto={true} />
                </div>
                <div>
                  <Button type="button" onClick={this.savePNG1} label="Save" className="p-button-success" style={{marginRight:'.25em'}} />
                  <Button label="Clear" className="p-button-secondary" onClick={this.resetSign1} />
                 <Button type="button" onClick={()=>this.setState({visibleFullScreen1:false})} label="X" className="p-button-danger"/>
                </div>
              </div>
            <div className="signature-pad--body">
              <canvas id='canvas1'></canvas>
            </div>
            <div className="signature-pad--footer">
              <div className="description">Sign above</div>
            </div>
          </div>
          </div>
          </div>
          {/* <Button type="button" onClick={this.savePNG1} label="Save" className="p-button-success" style={{marginRight:'.25em'}} />
          <Button type="button" onClick={()=>this.setState({visibleFullScreen1:false})} label="Cancel" className="p-button-secondary"/> */}
        </Sidebar>
        {/* <Sidebar visible={this.state.visibleFullScreen2} fullScreen={true} baseZIndex={1000000} onHide={()=>this.setState({visibleFullScreen2:false})} >
            <div id="signature-pad"  className="signature-pad">
              <div className="signature-pad--body">
                <canvas id='canvas2'></canvas>
              </div>
              <div className="signature-pad--footer">
                <div className="description">Sign above</div>
                <div className="signature-pad--actions">
                  <div>
                  <Ripples color="#fff" during={1200}><Button label="Clear" className="p-button-secondary" onClick={this.resetSign2} /></Ripples>
                  </div>
                </div>
              </div>
            </div>
            <br/>
            <Button type="button" onClick={this.savePNG2} label="Save" className="p-button-success" style={{marginRight:'.25em'}} />
          <Button type="button" onClick={()=>this.setState({visibleFullScreen2:false})} label="Cancel" className="p-button-secondary"/>
        </Sidebar> */}
        <Button icon="pi pi-th-large" label='Landscape/Portrait' onClick={()=>this.setState({visibleFullScreen1:true})}  style={{marginRight:'.25em'}} />
        {/* <Button icon="pi pi-th-large" label='Portrait' onClick={()=>this.setState({visibleFullScreen2:true})}  style={{marginRight:'.25em'}} /> */}
        </div>
        </header>
      </div>
    );
  };
    
    
}

export default App;
